'use client';

import { useEffect, useRef, useState } from 'react';

export default function Footer() {
  const [isImageLocked, setIsImageLocked] = useState(false);
  const imageRef = useRef<HTMLDivElement>(null);
  const footerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const handleScroll = () => {
      if (imageRef.current && footerRef.current) {
        const imageRect = imageRef.current.getBoundingClientRect();
        const footerRect = footerRef.current.getBoundingClientRect();

        // When the bottom of the image reaches the top of the footer, lock it
        if (imageRect.bottom <= footerRect.top) {
          setIsImageLocked(true);
        } else {
          setIsImageLocked(false);
        }
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  return (
    <>
      {/* Scrollable Image Section */}
      <div
        ref={imageRef}
        className={`relative w-full h-[250px] ${isImageLocked ? 'sticky bottom-0 z-10' : ''}`}
      >
        {/* Placeholder for your drawing - replace with your actual image */}
        <div className="w-full h-full bg-gradient-to-br from-slate-200 to-slate-300 flex items-center justify-center">
          <div className="text-slate-600 text-center">
            <div className="text-4xl mb-4">ðŸŽ¨</div>
            <p className="text-lg font-medium">Your Drawing Goes Here</p>
            <p className="text-sm">Replace this with your actual image</p>
          </div>
        </div>
      </div>

      {/* Green Footer */}
      <footer
        ref={footerRef}
        className="bg-green-800 text-white relative z-20"
      >
        <div className="mx-auto max-w-6xl px-6 sm:px-12 md:px-16 lg:px-20 py-16">
          <div className="text-center">
            <h3 className="text-2xl md:text-3xl font-semibold mb-4">Curator Blog</h3>
            <p className="text-green-100 mb-6">
              Exploring art, culture, and creativity
            </p>
            <div className="text-sm text-green-200">
              Â© {new Date().getFullYear()} Â· All rights reserved
            </div>
          </div>
        </div>
      </footer>
    </>
  );
}
